<template>
    <div class="header-content">
        <div class="header-wrap">
            <div class="header-logo" @click="menuBtnClick(1)">
                <div class="turu-logo-image">
                    <img src="@/assets/images/web/turu.png" alt="투루 로고 이미지">
                </div>
                <div class="line-area"></div>
                <div class="humax-logo-image">
                    <img src="@/assets/images/web/hm.png" alt="휴맥스 모빌리티 로고 이미지">
                </div>
            </div>

            
            <ul class="home-button">
                <li><a href="https://kr.humaxdigital.com/" target="_blank"><img class="home-image" src="@/assets/images/web/home.png" alt="홈 버튼 아이콘"></a></li>
                <li class="ham-btn" @click="hamClickEvent()"><img class="ham-image" src="@/assets/images/web/menu.png" alt="햄버거 모양 메뉴 버튼 아이콘"></li>
                <li class="ham-btn" @click="closeClickEvent()"><img class="close-image" src="@/assets/images/mobile/Mobile_close.png" alt="메뉴 닫기 버튼 아이콘"></li>
            </ul>
            <!--  @touchend="hamClickEvent()" -->
            <!--  @touchend="closeClickEvent()" -->
        </div>
        <div class="global-navigation">
            <ul class="gnb-list">
                <li class="menu" :class="{ active: currentActive === 1 }" @click="menuBtnClick(1)"><a href="#"><span>Turu</span></a></li>
                <li class="menu" :class="{ active: currentActive === 2 }" @click="menuBtnClick(2)"><a href="#"><span>휴맥스모빌리티 서비스</span></a></li>
                <li class="menu" :class="{ active: currentActive === 3 }" @click="menuBtnClick(3)"><a href="#"><span>휴맥스 그룹·파트너스</span></a></li>
            </ul>
        </div>
    </div>
</template>

<script>
import { computed, ref, watch } from 'vue'

export default {
    components: {
        
    },
    props: ['currentIndex'],
    emits: ['menuIndexNum'],
    setup(props, context) {     
        const gnbCheckNum = ref(0)
        const gnbTopValue = ref(null)
        const hamIconDisplay = ref("block")
        const closeIconDisplay = ref("none")
        const currentActive = ref(1)

        gnbTopValue.value = "-400%"


        const hamClickEvent = (() => {
            if(gnbCheckNum.value != 1){
                gnbCheckNum.value = 1
                gnbTopValue.value = "100%"
                hamIconDisplay.value = "none"
                closeIconDisplay.value = "block"
            }else {
                return false;
            }
        });
        
        const closeClickEvent = (() => {
            if(gnbCheckNum.value != 0){
                gnbCheckNum.value = 0
                gnbTopValue.value = "-400%"
                closeIconDisplay.value = "none"
                hamIconDisplay.value = "block"
            }else {
                return false;
            }
        });
        
        const menuBtnClick = ((menuIndex) => {
            if(menuIndex >= 1 && menuIndex <= 3){
                // emit
                context.emit("menuIndexNum", menuIndex)
                menuActiveCheck(menuIndex)
                closeClickEvent()
            }
            else {
                return false;
            }
        });
        
        const menuActiveCheck = ((activeIndex) => {
            currentActive.value = activeIndex
        });




        watch(() => props.currentIndex, (newVal) => {
            if(newVal < 5){
                closeClickEvent()
                menuActiveCheck(1)
            }
            else if(newVal < 13){
                menuActiveCheck(2)
            }
            else{
                menuActiveCheck(3)
            }
        });
        const headerMove = computed(() => {
            return props.currentIndex == 1 || props.currentIndex == 2 || props.currentIndex == 3 || props.currentIndex == 4 ? "-110px" : "0";
        });
        return {
            gnbCheckNum,
            gnbTopValue,
            hamIconDisplay,
            closeIconDisplay,
            hamClickEvent,
            closeClickEvent,
            menuBtnClick,
            menuActiveCheck,
            headerMove,
            currentActive
        }
    }
}

</script>

