<template>
    <!-- Modal -->
    <Transition name="modal">
      <div v-if="modalData" class="modal-wrapper" @click="closeModal">
      </div>
    </Transition>

    <Transition name="modal">
      <div v-if="modalData" class="modal">
        <div class="modal-close"><img src="@/assets/images/mobile/Mobile_close_white.png" /></div>
        <div class="modal-header">
          <p :class="modalData.idx < 4 ? 'selected' : null" @click="changeModalIndex(1)">휴맥스 그룹</p>
          <p :class="modalData.idx >= 4 ? 'selected' : null"  @click="changeModalIndex(4)">파트너스</p>
        </div>

        <div class="modal-content-container">
            <Transition :name="indexDirection ? 'modal-content' : 'modal-content-reverse'">
            <div :key="modalData.idx" class="modal-content">
                <div class="modal-content-title">{{ modalData.title }}</div>
                <div class="modal-content-image">
                <a :href="modalData.link" target="_blank"><img :src="modalData.imgSrc" /></a>
                <div class="modal-content-leftArrow" @click="decreaseIndex"><img src="@/assets/images/mobile/left_16.png" /></div>
                <div class="modal-content-rightArrow" @click="increaseIndex"><img src="@/assets/images/mobile/right_16.png" /></div>
                </div>
                <div class="modal-content-description">
                <p>{{ modalData.description }}</p>
                </div>
            </div>
            </Transition>
        </div>

      </div>
    </Transition>
</template>

<script>
import { computed, watch, ref } from 'vue'
import { useStore } from 'vuex'

export default {
    setup() {
        const store = useStore()
        const modalIndex = computed(() => store.state.modalIndex)
        const modalData = computed(() => store.state.modalData)
        const indexDirection = ref(0)
        const increaseIndex = () => {
            if (modalIndex.value < 12) {
                store.commit('increaseModalIndex')
            }
        }
        const decreaseIndex = () => {
            if (modalIndex.value > 1) {
                store.commit('decreaseModalIndex')  
            }
        }
        const closeModal = () => {
            store.commit('setModalData', null)
        }

        const changeModalIndex = (idx) => {
          store.commit('setModalIndex', idx)
        }

        watch(() => modalIndex.value, (oldVal, newVal) => {
            if (oldVal > newVal) {
                indexDirection.value = 0
            } else if (newVal > oldVal) {
                indexDirection.value = 1
            }
        })

        return {
            increaseIndex,
            decreaseIndex,
            closeModal,
            modalData,
            modalIndex,
            indexDirection,
            changeModalIndex
        }
    },
}
</script>

