          width: 20px;

        }

        .modal-content-rightArrow {
          cursor: pointer;
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          width: 20px;
        }
      }

      .modal-content-description {
        width: 100%;

        p {
          font-family: 'NanumSquareAcr';
