          padding: 0 8px;
          min-height: 161px;
        }
      }

      &.modal-content-enter-active, 
      &.modal-content-leave-active {
        transition: all .5s ease;
      }

      &.modal-content-enter-from {
        transform: translateX(-100%);
      }
      &.modal-content-leave-to {
        transform: translateX(100%);
      }

      &.modal-content-reverse-enter-active, 
      &.modal-content-reverse-leave-active {
        transition: all .5s ease;
      }

      &.modal-content-reverse-enter-from {
        transform: translateX(100%);
      }
      &.modal-content-reverse-leave-to {
