<script>
import { Vue3Lottie } from 'vue3-lottie';
import { ref } from 'vue'
import carLottie from '@/assets/lottie/carsharing_shadow.json'
import parkingLottie from '@/assets/lottie/parking_shadow.json'
import chargerLottie from '@/assets/lottie/charge_shadow.json'
import bizLottie from '@/assets/lottie/biz_shadow.json'


export default {
  components: { Vue3Lottie },
  props: ['currentIndex', 'scrolling'],
  setup() {
    const turuMobile = ref(false)
    let screenMobile = window.matchMedia('(max-width:767px)')

    if (screenMobile.matches) {
      turuMobile.value = true
    } else {
      turuMobile.value = false
    }

    return {
      carLottie,
      parkingLottie,
      chargerLottie,
      bizLottie,
      turuMobile
    }
  }
}
