                group: "partners",
                imgSrc: require("@/assets/images/web/argosdyne.png"),
                description: "아르고스다인은 드론 영상/센서 데이터를 실시간으로 처리하고, 드론 스테이션을 통해 자율 비행과 무인 운영 시스템을 제공하는 드론 모빌리티 플랫폼 기업입니다.",
