                group: "partners",
                imgSrc: require("@/assets/images/web/angel_plus.png"),
                description: `엔젤플러스의 1588-1004 대리운전 서비스는 철저하게 교육, 관리되는 기사 배치를 우선으로 국내 최고의 서비스와 안정성을 제공합니다.`,
                link: "http://1588-1004.co.kr/"            
              },
        ]);
        const humaxData = dataObject.filter(data => data.group === "humax");
        const partnerData = dataObject.filter(data => data.group === "partners");
        let screenMobile = window.matchMedia("(max-width: 767px)");
        const turuMobile = ref(false);
        const modalIndex = computed(() => store.state.modalIndex)

        onMounted(() => {
            companyWrapper.value.addEventListener('scroll', onScroll)
            if (screen459.matches) {
              screen459State.value = true
            }
        })
        
        onBeforeMount(() => {
            companyWrapper.value?.removeEventListener('scroll', onScroll)
            store.commit('setModalData', null)
        })


        if (screenMobile.matches) {
            turuMobile.value = true;
        }
        else {
            turuMobile.value = false;
        }
        const scrollToFirst = () => {
            ctx.emit("scrollToFirst");
        };
        const openModal = (idx) => {
          if (turuMobile.value) {
            store.commit('setModalData', {
              title: dataObject[idx-1].title,
              imgSrc: dataObject[idx-1].imgSrc,
              description: dataObject[idx-1].description,
              link: dataObject[idx-1].link,
              idx
            })
          }
        };

        watch(() => modalIndex.value, (newVal) => {
          if (newVal > 0 && newVal < 13) {
            openModal(newVal)
          }
        })

        watch(() => props.currentIndex, (newVal) => {
          if (newVal === 16) {
            companyWrapper.value.scrollTop = 0
          }
        })

        const humaxLink = () => {
          companyWrapper.value.scrollTop = 1
        }

        const partnerLink = () => {
          if (screen459.matches === true) {
            companyWrapper.value.scrollTop = 180
          } else if (screen869.matches === true) {
            companyWrapper.value.scrollTop = 1450
          } else if (screen1280.matches === true) {
            companyWrapper.value.scrollTop = 950
          } else {
            companyWrapper.value.scrollTop = 550
          }
        }

        const onScroll = () => {
            let newVal = companyWrapper.value.scrollTop
            ctx.emit('onScroll4', newVal)
            if (screen459.matches === true) {
                screen459State.value = true;
                if (newVal > 22) {
                    selectedLink.value = 2;
                }
                else {
                    selectedLink.value = 1;
                }
            }
            else if (screen869.matches === true) {
                screen459State.value = false;
                if (newVal > 100) {
                    selectedLink.value = 2;
                }
                else {
                    selectedLink.value = 1;
                }
            }
            else if (screen1280.matches === true) {
                screen459State.value = false;
                if (newVal > 850) {
                    selectedLink.value = 2;
                }
                else {
                    selectedLink.value = 1;
                }
            }
            else { // Full Size
                screen459State.value = false;
                if (newVal > 400) {
                    selectedLink.value = 2;
                }
                else {
                    selectedLink.value = 1;
                }
            }
        }

        return {
            dataObject,
            arrowUp,
            companyWrapper,
            selectedLink,
            scrollToFirst,
            humaxData,
            partnerData,
            screen459State,
            turuMobile,
            openModal,
            humaxLink,
            partnerLink
        };
    },
}
