<template>
    <div class="cover">
    </div>
    <HeaderPage class="header" :currentIndex="currentIndex" @menuIndexNum="menuIndexNum" />
    <div class="container">
        <div class="container-wrap">
            <Section01 class="section" />
            <Section02 class="section" :currentIndex="currentIndex" :scrolling="scrolling" />
            <Section03 class="section" :currentIndex="currentIndex" :scrolling="scrolling" />
            <Section04 class="section" :currentIndex="currentIndex" :currentScroll="currentScroll" :scrolling="scrolling" @scrollToFirst="currentIndex = 0" @onScroll4="onScroll4" />
        </div>
    </div>
    <ModalPage />
</template>

<script>
import HeaderPage from '@/components/HeaderPage.vue'
import Section01 from '@/components/sections/Section-01.vue'
import Section02 from '@/components/sections/Section-02.vue'
import Section03 from '@/components/sections/Section-03.vue'
import Section04 from '@/components/sections/Section-04.vue'
import ModalPage from '@/components/ModalPage.vue'

import { ref, computed, onMounted, onBeforeUnmount, watch } from 'vue'

export default {
    components: {
        HeaderPage,
        Section01,
        Section02,
        Section03,
        Section04,
        ModalPage
    },
    setup() {
        onMounted(() => {  
            window.addEventListener('resize', onResize)
            onResize()
            onParameter()
          
            window.addEventListener('wheel', onScroll)
            
            window.addEventListener('touchstart', onTouchStart)
            window.addEventListener('touchend', onTouchEnd)
            
            window.addEventListener('keydown', onKeyDown)

        })

        onBeforeUnmount(() => {     
            window.removeEventListener('resize', onResize)
            
            window.removeEventListener('wheel', onScroll)
        
            window.removeEventListener('touchstart', onTouchStart)
            window.removeEventListener('touchend', onTouchEnd)
            
            window.removeEventListener('keydown', onKeyDown)

        })


        // Variable
        const currentIndex = ref(0)    // 현재 Index 값
        const currentScroll = ref(0)
        const scrolling = ref(0)
        const timer = ref(null)
        const touchFinger = ref(0)
        const scroll4Top = ref(0)
        const appHeight = ref(0)

        // Computed
        const transformContainer = computed(() => {
            if (currentIndex.value <= 0) {
                return `translateY(0)`
            } else if (currentIndex.value < 5) {
                return  `translateY(-100vh)`
            
            } else if (currentIndex.value < 13) {
                return  `translateY(-200vh)`
            
            } else {
                return `translateY(-300vh)`
            }
        })
        
        const gapVhHeight = computed(() => {
            return `calc(100vh - ${appHeight.value}px)`
        })


        // Function
        const onParameter = () => {
            const param = window.location.search
            if(param === "?sectionNumber=03"){
                currentIndex.value = 5
            }
            else if(param === "?sectionNumber=04"){
                currentIndex.value = 13
            }
            else {
                currentIndex.value = 0
            }
        }
        
        const onResize = () => {
            appHeight.value = window.innerHeight
        }

        const onTouchStart = (e) => {
            touchFinger.value = e.changedTouches[0].clientY
        }
        
        const onTouchEnd = (e) => {
            const touchEndIn = e.changedTouches[0].clientY
            if (touchFinger.value > touchEndIn + 10) {
                scrolling.value = 1
            } else if (touchFinger.value < touchEndIn - 10) {
                scrolling.value = -1
            }
        
            clearTimeout(timer.value)
            timer.value = setTimeout(() => {
                scrolling.value = 0
            }, 100)
        }
        
        const onKeyDown = (e) => {
            if (e.key.toLowerCase() === 'arrowdown') {
                scrolling.value = 1
            } else if (e.key.toLowerCase() === 'arrowup') {
                scrolling.value = -1
            }
        
            clearTimeout(timer.value)
            timer.value = setTimeout(() => {
                scrolling.value = 0
            }, 10)
        }
        
        const onScroll = (e) => {
            if (e.wheelDelta < 0) {
                scrolling.value = 1
            } else if (e.wheelDelta > 0) {
                scrolling.value = -1
            }
        
            clearTimeout(timer.value)
            timer.value = setTimeout(() => {
                scrolling.value = 0
            }, 100)
        }
        
        const onScroll4 = (e) => {
          scroll4Top.value = e
        }

        const scrollUp = () => {
            if (currentIndex.value > 0) { 
                if (currentIndex.value > 12) {
                    if (scroll4Top.value < 5) {
                        currentIndex.value--
                    }
                } else {
                    currentIndex.value--

                }
            }
        }

        const scrollDown = () => {
            if (currentIndex.value < 13) {
                currentIndex.value++
            }
        }

        watch(scrolling, (newVal) => {
            if (newVal === 1) {
                scrollDown()
            } else if (newVal === -1) {
                scrollUp()
            }
        })

        watch(scroll4Top, (newVal, oldVal) => {
            if (newVal === 0 && oldVal > 0) {
                scrollUp()
            }
        })
                
        const menuIndexNum = ((value) => {
            if(value === 1) {
                currentIndex.value = 0
                history.replaceState({}, null, location.pathname);
                location.href = window.location+""
            }
            else if(value === 2){
                currentIndex.value = 5
                history.replaceState({}, null, location.pathname);
                location.href = window.location+"?sectionNumber=03"
            }
            else if(value === 3){
                currentIndex.value = 13
                history.replaceState({}, null, location.pathname);
                location.href = window.location+"?sectionNumber=04"
            }
        });
        return {
            gapVhHeight,
            transformContainer,
            currentIndex,
            currentScroll,
            scrolling,
            menuIndexNum,
            onScroll4,
        }
    }
}



</script>
<style lang="scss" scoped>
// font style list
/*
    font-family: 'NanumSquareLight';
    font-family: 'NanumSquare';
    font-family: 'NanumSquareBold';
    font-family: 'NanumSquareExtraBold';
    font-family: 'NanumSquareAcb';
    font-family: 'NanumSquareAceb';
    font-family: 'NanumSquareAcl';
    font-family: 'NanumSquareAcr';
*/
*, body {
    box-sizing: border-box;
    font-family: 'NanumSquare';
}

.container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    overflow: hidden;

    .container-wrap {
        height: 100%;
        transform: v-bind('transformContainer');
        transition: all .2s ease;
    }
    
    .section {
        box-sizing: border-box;
        width: 100vw;
        height: 100%;
        font-size: 50px;
        font-weight: bold;
        margin-bottom: v-bind('gapVhHeight');
  }
}   

</style>