<template>
  <section class="section-02">
    <div class="element-container" ref="elementContainer">
      <div class="inner-part">
        <div class="content-part">
          <div class="wording-part">
            <div class="wording-top">
              <p class="wording">모빌리티를<br />두루두루</p>
            </div>
            <div class="wording-bottom">
              <p class="wording">모두</p>
              <div class="connection-line"></div>
              <p class="wording dot">잇다</p>
            </div>
          </div>
  
          <div class="turu-logo">
            <img src="@/assets/images/web/turu.png" alt="투루 로고 이미지">
          </div>
        </div>
  
        <div class="interaction-part">
          <div class="left-top"></div>
          <div class="right-middle">
            <div class="orage-bar"></div>
            <div class="red-dot"></div>
            <div class="center-bottom"></div>
          </div>
        </div>

      </div>
    </div>
    <div class="interaction-wording"> 
      <div class="inter-wording wording-one">
        <p><span class="bold-font">이동부터 주, 정차 시간</span>까지 <br />
          두루 아우르는</p>
      </div>
      <div class="inter-wording wording-two">
        <p><span class="bold-font">다양한 모빌리티 서비스</span>들을 <br />
          <span class="bold-font">통합, 일원화하여</span></p>
      </div>
      <div class="inter-wording wording-three">
        <p>사용자에게 가장 <span class="bold-font">효율적이고<br />
          최적화된 경험</span>을 제공합니다.</p>
      </div>
      <div class="interaction-dot"></div>
    </div>
  </section>
</template>

<script>
// import { watch, computed } from 'vue';

import { watch, ref } from 'vue'
export default {
  components: {
    
  },
  props: ['currentIndex', 'scrolling'],
  setup(props) {
    // line interaction
    const elementContainer = ref(null);
    const elementHeight = ref(0)
    const elementHeightMobile = ref(0)
    const moveSec = ref(0)
    const leftTopMove = ref("-987px")
    const leftTopMoveMobile = ref("calc(-80vh - 205px)")
    const leftDelaySec = ref("0.5s")
    const orangeBarMove = ref("150%")
    const orangeBarMoveMobile = ref("calc(100% + 56px)")
    const orangeDelaySec = ref("1s")
    const yellowBarLeft = ref("150%")
    const yellowBarHeight = ref("218px")
    const yellowBarHeightMobile = ref("120px")
    const yellowDelaySec = ref("1.5s")
    
    // interaction wording & dot
    const wordingOneOpacity = ref(0)
    const wordingTwoOpacity = ref(0)
    const wordingThreeOpacity = ref(0)
    const interactionDotOpacity = ref(0)
    const interactionDotLeft = ref("calc(50% + 272px)")
    const interactionDotTop = ref("calc(30vh + 44px)")
    const interactionDotLeftMobile = ref("244px")
    const interactionDotTopMobile = ref("calc(30vh + 89px)")


    watch(() => props.scrolling, (scNewVal) => {
      if(scNewVal == 1){
        elementContainer.value.classList.remove("scrollUp")
        elementContainer.value.classList.remove("scrollDown")
        elementContainer.value.classList.add("scrollDown")
      }
      else if(scNewVal == -1){
        elementContainer.value.classList.remove("scrollUp")
        elementContainer.value.classList.remove("scrollDown")
        elementContainer.value.classList.add("scrollUp")
      }

    })
    watch(() => props.currentIndex, (newVal) => {
      if(newVal == 0){
        elementZero()
      }
      else if(newVal == 1){
        elementfirst()
      }
      else if(newVal == 2){
        elementMoveSecond()
      }
      else if(newVal == 3){
        elementMoveThird()
      }
      else if(newVal == 4){
        elementMoveFourth()
      }
    })

    const elementZero = (() => {
      moveSec.value = "0.2s"
      leftTopMove.value = "-987px"
      leftTopMoveMobile.value = "calc(-80vh - 205px)"
      leftDelaySec.value = "0"
      orangeBarMove.value = "150%"
      orangeBarMoveMobile.value = "calc(100% + 56px)"
      orangeDelaySec.value = "0"
      yellowBarLeft.value = "150%"
      yellowBarHeight.value = "218px"
      yellowBarHeightMobile.value = "120px"
      yellowDelaySec.value = "0"
    })
    
    const elementfirst = (() => {
      elementHeight.value = "0"
      elementHeightMobile.value = "0"
      moveSec.value = "0.5s"
      leftTopMove.value = "-537px"
      leftTopMoveMobile.value = "-205px"
      leftDelaySec.value = "0.5s"
      orangeBarMove.value = "calc(50% - 109px)"
      orangeBarMoveMobile.value = "56px"
      orangeDelaySec.value = "1s"
      yellowBarLeft.value = "calc(50% - 109px)"
      yellowBarHeight.value = "270vh"
      yellowBarHeightMobile.value = "260vh"
      yellowDelaySec.value = "1.5s"
      wordingOneOpacity.value = 0
      wordingTwoOpacity.value = 0
      wordingThreeOpacity.value = 0
      interactionDotOpacity.value = 0
    })
    
    const elementMoveSecond = (() => {
      elementHeight.value = "-300vh"
      elementHeightMobile.value = "-300vh"
      moveSec.value = "0.5s"
      yellowBarHeight.value = "270vh"
      yellowBarHeightMobile.value = "260vh"
      yellowDelaySec.value = "0"
      wordingOneOpacity.value = 1
      wordingTwoOpacity.value = 0
      wordingThreeOpacity.value = 0
      interactionDotOpacity.value = 1
      interactionDotLeft.value = "calc(50% + 272px)"
      interactionDotTop.value = "calc(30vh + 44px)"
      interactionDotLeftMobile.value = "204px"
      interactionDotTopMobile.value = "calc(30vh + 89px)"
    })
    
    const elementMoveThird = (() => {
      elementHeight.value = "-280vh"
      elementHeightMobile.value = "-280vh"
      moveSec.value = "0.5s"
      wordingOneOpacity.value = 0
      wordingTwoOpacity.value = 1
      wordingThreeOpacity.value = 0
      interactionDotOpacity.value = 1
      interactionDotLeft.value = "calc(50% + 120px)"
      interactionDotTop.value = "calc(50vh + 89px)"
      interactionDotLeftMobile.value = "242px"
      interactionDotTopMobile.value = "calc(50vh + 89px)"
    })
    
    const elementMoveFourth = (() => {
      leftTopMove.value = "-537px"
      leftTopMoveMobile.value = "-205px"
      leftDelaySec.value = "0.5s"
      orangeBarMove.value = "calc(50% - 109px)"
      orangeBarMoveMobile.value = "56px"
      orangeDelaySec.value = "1s"
      yellowBarLeft.value = "calc(50% - 109px)"
      yellowBarHeight.value = "270vh"
      yellowBarHeightMobile.value = "260vh"
      yellowDelaySec.value = "1.5s"

      elementHeight.value = "-260vh"
      elementHeightMobile.value = "-260vh"
      moveSec.value = "0.2s"
      wordingTwoOpacity.value = 0
      wordingThreeOpacity.value = 1
      interactionDotLeft.value = "calc(50% + 191px)"
      interactionDotTop.value = "calc(70vh + 89px)"
      interactionDotLeftMobile.value = "334px"
      interactionDotTopMobile.value = "calc(70vh + 89px)"
    })

    return {
      elementContainer,
      elementHeight,
      elementHeightMobile,
      moveSec,
      leftTopMove,
      leftTopMoveMobile,
      leftDelaySec,
      orangeBarMove,
      orangeBarMoveMobile,
      orangeDelaySec,
      yellowBarLeft,
      yellowBarHeight,
      yellowBarHeightMobile,
      yellowDelaySec,
      wordingOneOpacity,
      wordingTwoOpacity,
      wordingThreeOpacity,
      interactionDotOpacity,
      interactionDotLeft,
      interactionDotTop,
      interactionDotLeftMobile,
      interactionDotTopMobile,
    }
  }
}
</script>

